	var Utils=(function(){
		function Utils(){};
		__class(Utils,'com.isnowfox.core.io.Utils');
		Utils.putThree=function(out,v){
			if (out.endian=="bigEndian"){
				out.writeByte(v >>> 16);
				out.writeByte((v >>> 8));
				out.writeByte((v >>> 0));
			}
			else{
				out.writeByte(v >>> 0);
				out.writeByte((v >>> 8));
				out.writeByte((v >>> 16));
			}
		}

		Utils.getThree=function(in0){
			var ch1=0;
			var ch2=0;
			var ch3=0;
			if (in0.endian=="bigEndian"){
				ch1=in0.getUint8();
				ch2=in0.getUint8();
				ch3=in0.getUint8();
				return ((ch1 << 16)+(ch2 << 8)+(ch3 << 0));
			}
			else{
				ch1=in0.getUint8();
				ch2=in0.getUint8();
				ch3=in0.getUint8();
				return ((ch1 << 0)+(ch2 << 8)+(ch3 << 16));
			}
		}

		return Utils;
	})()


