			this._gameRoomInfo.chapter=null;
			ChapterResultDialog.showDialog(msg,this._gameRoomInfo,Handler.create(this,function(){
				ChapterResultDialog.close();
			}));
			this._gameTableScene.updateEndResult(msg,this._gameRoomInfo);
