	//class mj.model.PaiUtils
	var PaiUtils=(function(){
		function PaiUtils(){}
		__class(PaiUtils,'mj.model.PaiUtils');
		PaiUtils.toString=function(pai){
			if (pai < 9){
				return (pai+1)+""
			}
			else if (pai < 18){
				return (pai-8)+""
			}
			else if (pai < 27){
				return (pai-17)+""
			}
			else{
				switch (pai){
					case 27:
						return "";
					case 28:
						return "";
					case 29:
						return "";
					case 30:
						return "";
					case 31:
						return "";
					case 32:
						return "";
					case 33:
						return "";
					}
			}
			return null;
		}

		PaiUtils.checkIsHuiEr=function(pai){
			var huiEr=GameManager.instance.huiEr;
			if (huiEr==null || huiEr.length==0){
				return false;
			}
			for (var i=0;i < huiEr.length;i++){
				var huierItem=huiEr[i];
				if (huierItem==pai){
					return true;
				}
			}
			return false;
		}

		PaiUtils.showPaiIcon=function(paiImage,pai){
			if (PaiUtils.checkIsHuiEr(pai)){
				var huiErImage=new Image();
				huiErImage.skin=paiImage.skin.replace(/[0-9]+\.png/gi,"hun.png");
				paiImage.addChild(huiErImage);
			}
			paiImage.skin=paiImage.skin.replace(/[0-9]+\.png/gi,pai.toString()+".png");
		}

		PaiUtils.toUserPlace=function(result){
			var m=new UserPlaceMsg();
			if (result.queTou !=mj.model.PaiUtils.NOT_PAI_INDEX){
				var shouPai=[];
				var i=0;
				var pai=0;
				for (i=0;i < result.shunZi.length;i++){
					pai=result.shunZi[i];
					shouPai.push(pai,pai+1,pai+2);
				}
				for (i=0;i < result.keZi.length;i++){
					pai=result.keZi[i];
					shouPai.push(pai,pai,pai);
				}
				shouPai.push(result.queTou,result.queTou);
				m.shouPai=shouPai;
			}
			else{
				m.shouPai=result.shouPai;
			}
			m.anGang=result.anGang;
			m.daMingGang=result.daMingGang;
			m.xiaoMingGang=result.xiaoMingGang;
			m.chi=result.chi;
			m.peng=result.peng;
			return m;
		}

		PaiUtils.HAS_PAI_INDEX=-2;
		PaiUtils.NOT_PAI_INDEX=-1;
		return PaiUtils;
	})()


